import React from "react"
import { Input } from "./input";

interface INumberLockViewProps {
    onBack: () => void;
    onDone: () => void;
}

export function NumberLockView(props: INumberLockViewProps): JSX.Element {
    const validateInput = (key: string) => {
        if (!+key) {
            return undefined;
        }

        return key;
    }

    return (
        <div className="lock">
            <div className="txt">
                <h1>Computer repair</h1>
                <p>In order to escape the red planet, you need to use the computer. Unfortunately, the computer's emoji unit is miscalibrated and the computer won't function without it. You need to fix it.</p>
                <p>To fix the computer, you need to provide it with the correct emoji values.</p>
                <p>Looking at the computer's memory, you see how some of the emojis add up together. This gives you an idea...</p>
            </div>
            <div className="eq">
                <p>🪐 + 🪐 = 10</p>
                <p>🪐 + 🌌 = 14</p>
                <p>🌌 + ☄️ = 16</p>
                <p>☄️ + 🛰️ = 11</p>
            </div>
            <div className="eqinp">
                <Input expected="7549" onDone={props.onDone} validateInput={validateInput}></Input>
                <span style={{ gridColumn: 1, gridRow: 2 }}>☄️</span>
                <span style={{ gridColumn: 2, gridRow: 2 }}>🪐</span>
                <span style={{ gridColumn: 3, gridRow: 2 }}>🛰️</span>
                <span style={{ gridColumn: 4, gridRow: 2 }}>🌌</span>
            </div>
            <div>
                <p>... or you could always <a href="#" onClick={props.onBack}>go back</a> and try again later.</p>
            </div>
        </div>);
}

